@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}
.App {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}
#mode-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
#poemboard {
  width: 80%;
  /* height: inherit; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -150px;
}

#poem-title {
  font-size: xxx-large;
  flex-wrap: wrap;
}
#poem-body {
  margin-top: 15px;
  margin-left: 10px;
}
#poem-content {
  font-size: x-large;
  line-height: 1.3;
}

@media screen and (max-width: 480px) {
  #poemboard {
    width: 80%;
  }
  #poem-title {
    font-size: x-large;
    flex-wrap: wrap;
  }
  #poem-body {
    margin-top: 15px;
    margin-left: 0px;
  }
  #poem-content {
    font-size: 14px;
    line-height: 1.3;
  }
}
